.biyooon {
  animation: biyooon 4s infinite;
}

@keyframes biyooon {
  0% {
      transform: scale(1, 0.8);
  }
  /* 20% {
      transform: scale(1, 1);
  }
  45% {
      transform: scale(1, 1.15);
  } */
  40% {
      transform: scale(0.6, 1.8);
  }
  70% {
      transform: scale(1.9, 0.8);
  }
  100% {
      transform: scale(1, 0.8);
  }
}

.mocchiri {
  animation: mocchiri 3s infinite;
}

@keyframes mocchiri {
  0% {
      transform: scale(1, 0.8);
  }
  20% {
      transform: scale(0.8, 1.1);
  }
  95% {
      transform: scale(1, 1);
  }
  100% {
      transform: scale(1, 0.8);
  }

}
